'use client';
import React from 'react';

export const ChatbotIcon = () => (
  <button
    type="button"
    title="Chat with us"
    className="position-fixed rounded-circle shadow-lg d-flex align-items-center justify-content-center"
    style={{
      right: 24,
      bottom: 24,
      width: 60,
      height: 60,
      background: '#0d6efd',
      color: '#fff',
      border: 'none',
      zIndex: 1050,
      transition: 'box-shadow 0.2s, background 0.2s',
      fontSize: 28,
      cursor: 'pointer',
    }}
    onMouseOver={e => (e.currentTarget.style.background = '#084298')}
    onMouseOut={e => (e.currentTarget.style.background = '#0d6efd')}
    aria-label="Open chatbot"
  >
    <span role="img" aria-label="chatbot">ü§ñ</span>
  </button>
);

